import { createRouter, createWebHashHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    name: '/',
    redirect: '/menu'
  },
  {
    path: '/menu',
    name: 'menu',
    component: () => import('../views/Menu.vue')
  },
  {
    path: '/newInfo',
    name: 'newInfo',
    component: () => import('../views/NewInfo.vue'),
  },
  {
    path: '/roleInfo',
    name: 'roleInfo',
    component: () => import('../views/RoleInfo.vue')
  },
  {
    path: '/picture',
    name: 'picture',
    component: () => import('../views/Picture.vue')
  },
  {
    path: '/newsDetail',
    name: 'newsDetail',
    component: () => import('../views/NewsDetail.vue')
  },
  {
    path: '/roleDetail',
    name: 'roleDetail',
    component: () => import('../views/RoleDetail.vue')
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/login.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
